@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500&family=Zen+Kurenaido&display=swap');

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body {
  background-color: #f6f7f9;
  font-family: 'Roboto', sans-serif;
}

h1 {
  font-weight: 300;
  font-size: 60px;
}

a:focus {
  outline: none;
}

button {
  text-decoration: none;
  border: 1px solid #1b262b;
  font-family: inherit;
  font-size: 18px;
  background-color: #f6f7f9;
  color: #000000;
  padding: 17px 35px;
  border-radius: 5px;
}

button:active {
  box-shadow: inset 5px 5px 30px -2px rgba(0, 0, 0, 0.38);
}

.header {
  color: #f6f7f9;
  display: flex;
  justify-content: space-between;
  padding: 0 100px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1500;
  transition: all 1s;
}

.header-logo {
  font-family: 'Zen Kurenaido', sans-serif;
  font-size: 40px;
  letter-spacing: 10px;
  padding: 25px 40px 35px;
}

.menu-tablet,
.burger-menu {
  display: none;
}

.menu-list {
  display: flex;
  justify-content: space-between;
  font-size: 20px;
  text-align: center;
}

.menu-list > li {
  padding: 40px 30px;
  cursor: pointer;
  position: relative;
}

.menu-list > li::before {
  content: ' ';
  display: block;
  position: absolute;
  width: 0;
  height: 2px;
  background-color: #f6f7f9;
  bottom: 30px;
  transition: width cubic-bezier(0.165, 0.84, 0.44, 1) 0.4s;
}

.menu-list > li:hover::before {
  width: calc(100% - 60px);
  background-color: var(--bcolor);
}

.menu-underline::before {
  width: calc(100% - 60px) !important;
  background-color: var(--bcolor) !important;
}

.main {
  position: relative;
}

.home,
.timer,
.whatWeDo,
.contact {
  padding: 160px 130px 60px;
  min-height: 100vh;
}

.social {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 60px;
  display: flex;
  border-right: 2px solid #f6f7f9;
  flex-direction: column;
  padding: 0;
  z-index: 1001;
  height: auto;
  transition: all 0.5s;
}

.social-phone {
  position: fixed;
  right: 0;
  top: 0;
  flex-direction: column;
  padding: 0;
  z-index: 1001;
  height: auto;
  transition: all 0.5s;
  display: none;
}

.social-link {
  padding: 15px 30px;
  text-decoration: none;
  color: inherit;
  transition: all 0.5s;
}

.social-icon {
  width: 24px;
  height: 24px;
  box-sizing: content-box;
  color: inherit;
}

.social-link:first-child {
  margin-top: auto;
}

.toTop {
  position: fixed;
  right: 0;
  bottom: 60px;
  top: 0;
  display: flex;
  flex-direction: column;
  border-left: 2px solid #f6f7f9;
  padding: 0;
  z-index: 1001;
  height: auto;
  min-width: 83px;
  transition: all 0.5s;
}

.toTop-link {
  writing-mode: vertical-lr;
  transform: rotate(180deg);
  display: flex;
  text-decoration: none;
  color: #f6f7f9;
  margin-top: auto;
  transition: all 0.5s;
}

.toTop-link > * {
  padding: 10px 30px;
}

.toTop-arrow {
  font-size: 21px;
  font-weight: bold;
}

.home {
  background: url(./img/cove.jpg) no-repeat center center #1b262b;
  background-size: cover;
  color: #f6f7f9;
  position: relative;
}

.home-text {
  width: 40vw;
  margin-top: 15vh;
  line-height: 80px;
  position: relative;
}

.home-button {
  margin-top: 60px;
}

.credits {
  position: absolute;
  bottom: 60px;
  right: 130px;
  text-transform: capitalize;
}

.timer {
  background: url(./img/stairs.jpg) no-repeat center center #7c7566;
  background-size: cover;
  color: #f6f7f9;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  position: relative;
  z-index: 10;
}

.timer-time {
  display: flex;
  justify-content: space-between;
  padding-top: 12vh;
}

.timer-time-container {
  margin: 0 20px;
}

.timer-time-container-date {
  font-size: 60px;
  font-weight: 200;
}

.timer-time-container-text {
  margin-top: 20px;
  font-size: 18px;
  font-weight: 300;
  text-transform: capitalize;
}

.timer-text {
  margin: 70px 0;
  font-size: 50px;
  font-weight: 300;
  max-width: 70vw;
}

.timer-button,
.contact-form-submit {
  background-color: #0059e6;
  border-color: #014bc2;
  color: #f6f7f9;
}

.whatWeDo {
  background: url(./img/waterfall.jpg) no-repeat center center #565656;
  background-size: cover;
  color: #f6f7f9;
  display: flex;
  justify-content: space-between;
  padding-top: 25vh;
  position: relative;
  z-index: 10;
}

.whatWeDo::before,
.timer::before,
.home::before {
  content: ' ';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 11;
  background-color: rgba(0, 0, 0, 0.5);
}

.whatWeDo > *,
.home > *,
.timer > * {
  z-index: 12;
}

.whatWeDo-text {
  width: 45%;
  max-width: 45%;
  margin-right: 7%;
}

.whatWeDo-text-header {
  font-size: 50px;
  font-weight: 300;
  margin-bottom: 50px;
}

.whatWeDo-text-paragraph {
  font-size: 18px;
  line-height: 30px;
}

.whatWeDo-iconContainer {
  max-width: 45%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  font-size: 25px;
}

.whatWeDo-iconContainer-icons {
  min-width: 200px;
}

.whatWeDo-iconContainer-icons-image,
.contact-icons-container-image {
  font-size: 70px;
  text-align: left;
  margin-bottom: 30px;
  width: 70px;
  height: 70px;
}

.contact {
  color: #000000;
}

.contact-text-header {
  font-family: 'Zen Kurenaido', sans-serif;
  font-size: 60px;
}

.contact-text-paragraph {
  margin-top: 30px;
  line-height: 30px;
  font-size: 18px;
  max-width: 45%;
}

.contact-container {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  font-size: 18px;
}

.contact-icons {
  max-width: 45%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.contact-icons-container {
  margin-top: 60px;
  flex: 1 1 250px;
  max-width: 300px;
}

.contact-icons-container-image {
  color: #0059e6;
}

.contact-icons-container-text {
  line-height: 30px;
}

.contact-form {
  max-width: 45%;
  margin-top: 60px;
  padding-right: 3%;
}

.contact-form input,
.contact-form #message {
  line-height: 40px;
  padding: 0 10px;
  border: 1px solid #999999;
  border-radius: 3px;
}

.contact-form input#name {
  width: 49%;
  margin-right: 2%;
}

.contact-form input#email {
  width: 49%;
}

.contact-form input#subject {
  margin-top: 20px;
  width: 100%;
}

.contact-form #message {
  margin-top: 20px;
  width: 100%;
  height: 250px;
  resize: none;
  line-height: 20px;
  padding: 10px;
  font-family: 'Roboto', sans-serif;
}

.contact-form-submit {
  margin-top: 40px;
}

/* responsive */

@media only screen and (max-width: 1280px) {
  .home,
  .timer,
  .whatWeDo,
  .contact {
    padding-top: 150px;
  }

  .contact-icons-container {
    flex-basis: 200px;
  }

  .whatWeDo-iconContainer {
    max-width: 48%;
  }

  .whatWeDo-iconContainer-icons {
    min-width: 0;
    flex-basis: 164px;
  }

  .home-text {
    width: 80%;
  }

  .contact-form {
    margin-top: -85px;
    padding-right: 0;
  }
}

@media only screen and (max-width: 1050px) {
  h1,
  .contact-text-header {
    font-size: 50px;
  }

  .timer-text {
    font-size: 43px;
  }

  .home-text h1 {
    line-height: 65px;
  }

  .home-text {
    width: 100%;
  }

  .contact-text-paragraph {
    max-width: 70%;
  }

  .contact-container {
    flex-wrap: wrap;
  }

  .contact-container > * {
    max-width: 100%;
    width: 100%;
  }

  .contact-form {
    margin-top: 80px;
    padding-right: 70px;
  }

  .whatWeDo {
    flex-wrap: wrap;
  }

  .whatWeDo > * {
    max-width: 100%;
    width: 100%;
    margin-right: 0;
  }

  .whatWeDo-text-paragraph {
    max-width: 70%;
  }

  .whatWeDo-iconContainer {
    margin-top: 20px;
    margin-bottom: 60px;
  }

  .whatWeDo-iconContainer-icons {
    margin-top: 40px;
    flex: 1 200px;
  }

  .menu-list {
    display: none;
  }

  .menu-tablet {
    display: flex;
    justify-content: space-between;
  }

  .menu-tablet li {
    padding: 31px 18px 20px;
    cursor: pointer;
    border-bottom-left-radius: 30%;
    border-bottom-right-radius: 30%;
    position: relative;

    transition: background-color 0.5s;
  }

  .menu-tablet li::before,
  .menu-tablet li::after {
    content: '';
    position: absolute;
    top: -30px;
    width: 30px;
    height: 30px;
    background-color: transparent;
    border-radius: 50%;
    transition: top cubic-bezier(0.165, 0.84, 0.44, 1) 0.9s;
  }

  .menu-tablet li::before {
    left: -30px;
    box-shadow: 15px -15px 0 var(--backColor);
  }

  .menu-tablet li::after {
    right: -30px;
    box-shadow: -15px -15px 0 var(--backColor);
  }

  .menuActive {
    background-color: var(--backColor);
    color: var(--iconColor);
  }

  .menuActive::before,
  .menuActive::after {
    top: 0 !important;
  }

  .menu-tablet svg {
    width: 35px;
    height: 35px;
  }
}

@media only screen and (max-width: 700px) {
  .social-link {
    padding: 15px 17px;
  }

  .toTop {
    min-width: 57px;
    overflow: hidden;
  }

  .toTop-link > * {
    padding: 10px 17px;
  }

  .home,
  .timer,
  .whatWeDo,
  .contact {
    padding: 110px 80px 60px;
  }

  .credits {
    right: 80px;
  }

  h1 {
    font-size: 40px;
  }

  .home-text {
    margin-top: 50%;
  }

  .header {
    padding: 0 57px 0 80px;
  }

  .header-logo {
    padding: 20px;
  }

  .timer-text {
    font-size: 35px;
  }

  .timer-time {
    padding-top: 30%;
  }

  .timer-time-container-date {
    font-size: 45px;
  }

  .timer-time-container-text {
    font-size: 16px;
  }

  .timer-time-container {
    margin: 0 13px;
  }

  .whatWeDo-text-paragraph {
    max-width: 100%;
  }

  .whatWeDo-text-header {
    font-size: 45px;
  }

  .whatWeDo-iconContainer-icons {
    flex-basis: 132px;
  }

  .whatWeDo-iconContainer-icons-image {
    width: 55px;
    height: 55px;
  }

  .whatWeDo-iconContainer {
    font-size: 20px;
  }

  .whatWeDo-iconContainer-icons-name {
    max-width: 80%;
  }

  .contact-text-paragraph {
    max-width: 100%;
  }

  .contact-icons-container-image {
    width: 55px;
    height: 55px;
  }

  .contact-icons-container {
    flex-basis: 150px;
    margin-top: 40px;
  }

  .contact-form {
    padding: 0;
    margin-top: 60px;
  }

  .menu-tablet {
    display: none;
  }

  .burger-menu {
    display: flex;
    flex-direction: column;
    padding: 20px 15px;
    position: relative;
    transition: background-color ease-in 0.35s;
  }

  .burger-menu-icon {
    cursor: pointer;
    width: 47px;
    height: 47px;
    z-index: 1520;
    transition: all 0.5s;
    transition: transform 0.5s;
  }

  .burger-active .burger-menu-icon {
    transform: rotate(90deg);
    color: #000000;
    transition: transform 0.5s;
  }

  .burger-active.burger-menu {
    background-color: #f6f7f9;
    box-shadow: -4px 0 8px rgba(0, 0, 0, 0.5);
    transition: background-color ease-in 0.35s;
  }

  .burger-menu-list {
    position: absolute;
    right: 0;
    top: 75px;
    z-index: 1519;
    color: #000000;
    background-color: #f6f7f9;
    height: 0;
    overflow: hidden;
    border-right: 1px solid #f6f7f9;
    border-bottom-left-radius: 30px;
    box-shadow: -4px 8px 8px rgba(0, 0, 0, 0.5);
    transition: height 0.5s;
  }

  .burger-active .burger-menu-list {
    height: 252px;
  }

  .burger-menu-list li > svg {
    width: 40px;
    height: 40px;
  }

  .burger-menu-list li {
    padding: 10px 18px;
    cursor: pointer;
  }

  .burger-menu-list li:hover {
    background-color: rgba(0, 0, 0, 0.1);
  }
}

@media only screen and (max-width: 512px) {
  .social {
    display: none;
  }

  .home,
  .timer,
  .whatWeDo,
  .contact {
    padding-left: 20px;
    min-height: 0;
  }

  .home, .timer {
    padding-bottom: 180px;
  }

  .header {
    padding-left: 10px;
  }

  h1 {
    font-size: 35px;
  }

  .home-text h1 {
    line-height: 40px;
  }

  .home-button {
    margin-top: 25px;
  }

  .credits {
    font-size: 14px;
  }

  .timer-time-container-date {
    font-size: 35px;
  }

  .timer-time-container-text {
    font-size: 14px;
  }

  .timer-text {
    font-size: 30px;
  }

  .timer-time {
    padding-top: 10vh;
  }

  .whatWeDo-text-header {
    font-size: 35px;
  }

  .contact-icons-container-image {
    width: 40px;
    height: 40px;
  }

  .contact-icons-container-text {
    font-size: 14px;
    line-height: 20px;
  }

  .contact-icons-container {
    flex-basis: 130px;
  }

  .contact-text-paragraph {
    font-size: 16px;
    line-height: 24px;
  }

  .contact-form input {
    width: 100% !important;
    margin: 10px 0 0;
  }

  .contact-form {
    margin-top: 40px;
    font-size: 16px;
  }

  .contact-form-submit {
    margin-top: 20px;
  }

  .contact-form #message {
    height: 160px;
  }

  button {
    padding: 15px 25px;
  }

  .social-icon {
    width: 22px;
    height: 22px;
  }

  .social-phone {
    margin-top: 17px;
    display: flex;
  }

  .burger-menu-icon {
    width: 32px;
    height: 32px;
  }

  .burger-menu-list li > svg {
    width: 25px;
    height: 25px;
  }

  .burger-active .burger-menu-list {
    height: 195px;
  }

  .burger-menu {
    padding: 28px 15px;
  }
}
